<template>
	<div style="width: 100%;height: 100%;">
		<div ref="chartBox" class="echart-box show"></div>
		<div class="tool-box">
			<el-popover placement="right" width="200" trigger="click">
				<el-row>
					<el-col :span="10" class="label-text">
						数据类型：
					</el-col>
					<el-col :span="14">
						<el-select style="width: 120px;" v-model="dataType" placeholder="数据类型">
							<el-option label="人口数据" value="population"></el-option>
							<el-option label="经济数据" value="gdp"></el-option>
							<el-option label="工资数据" value="salary"></el-option>
							<el-option label="房价数据" value="house-price"></el-option>
						</el-select>
					</el-col>
				</el-row>
				<p></p>
				<el-row>
					<el-col :span="10" class="label-text">
						年份：
					</el-col>
					<el-col :span="14">
						<el-select style="width: 120px;" v-model="yearData" placeholder="数据年份">
							<el-option v-for="item, index in dataList" :key="index" :label="index+2011"
								:value="index+2011">
							</el-option>
						</el-select>
					</el-col>
				</el-row>
				<p></p>
				<el-row>
					<el-col :span="10" class="label-text">
						自动播放：
					</el-col>
					<el-col :span="14">
						<el-switch v-model="autoPlay"> </el-switch>
					</el-col>
				</el-row>
				<sub>数据来源于网络</sub>
				<i class="el-icon-s-tools" slot="reference"></i>
			</el-popover>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Chart',
		data() {
			return {
				dataList: [],
				visible: false,
				dataType: 'population',
				option: null,
				mapOption: null,
				barOption: null,
				chart: null,
				yearData: 2020,
				autoPlay: false,
				marks: {
					2011: '2011',
					2020: '2020'
				},
				timer: null
			}
		},
		methods: {
			initChart(map) {
				this.chart = this.$echarts.getInstanceByDom(this.$refs.chartBox);
				if (!this.chart) {
					this.chart = this.$echarts.init(this.$refs.chartBox);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(this.chart)
					})
				}
				this.initOption();
				this.setDataInfo();
				this.$refs.chartBox.onclick = this.changeOption;
				window.addEventListener("resize", this.chart.resize);
			},
			initOption() {
				this.mapOption = {
					title: {
						left: 'center'
					},
					tooltip: {
						trigger: 'item',
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					visualMap: {
						text: ['High', 'Low'],
						realtime: false,
						calculable: true,
						inRange: {
							color: ['rgba(64,158,255,0.2)', 'rgba(64,158,255,0.6)', 'rgba(64,158,255,1)']
						}
					},
					series: {
						id: 'population',
						animationDurationUpdate: 1000,
						universalTransition: true,
						map: "China",
						type: "map",
						aspectScale: 1.0,
						selectedMode: 'single',
						hoverable: false,
						roam: true,
						itemStyle: {
							normal: {
								borderWidth: 1,
								borderColor: 'rgba(100,100,100,0.1)',
								areaColor: '#fff',
								label: {
									show: false,
									textStyle: {
										color: '#ffffff',
										fontSize: 10
									}
								}
							},
							emphasis: {
								borderWidth: 1,
								borderColor: 'rgba(64,158,255,0.5)',
								areaColor: 'rgba(64,158,255,1)',
								label: {
									show: true,
									textStyle: {
										color: '#efefef'
									}
								}
							}
						}
					}
				}
				this.barOption = {
					color: ['rgba(64,158,255,0.5)'],
					title: {
						left: 'center'
					},
					tooltip: {
						trigger: 'item',
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					xAxis: {
						type: 'value',
						animationDuration: 1000,
						animationDurationUpdate: 1000
					},
					yAxis: {
						type: 'category',
						axisLabel: {
							rotate: 30,
							interval: 0,
							textStyle: {
								color: '#6495ED',
								fontSize: 10
							}
						},
						animationDuration: 1000,
						animationDurationUpdate: 1000
					},
					animationDurationUpdate: 1000,
					series: {
						type: 'bar',
						id: 'population',
						universalTransition: true,
						label: {
							show: true,
							color: 'rgba(50,50,50,0.7)',
							position: 'right',
							valueAnimation: true,
							fontFamily: 'monospace'
						}
					}
				}
				this.option = this.mapOption;
			},
			changeOption() {
				this.option = this.option == this.mapOption ? this.barOption : this.mapOption;
				this.chart.setOption(this.option, true);
			},
			setYearData() {
				this.mapOption.title.subtext = '地图  ' +  this.yearData + '年';
				this.barOption.title.subtext = '柱状图  ' + this.yearData + '年';
				this.mapOption.series.data = this.dataList[this.yearData - 2011];
				this.barOption.yAxis.data = this.dataList[this.yearData - 2011].map(function(item) {
					return item.name;
				});
				this.barOption.series.data = this.dataList[this.yearData - 2011].map(function(item) {
					return item.value;
				})
				this.chart.setOption(this.option, true);
			},
			setDataInfo() {
				if (this.dataType == 'population') {
					this.mapOption.title.text = this.barOption.title.text = "省份人口数据";
					this.mapOption.tooltip.formatter = this.barOption.tooltip.formatter =
						"<b>{a}</b> <br/>{b} ： <i>{c} 万人</i>";
					this.mapOption.series.name = this.barOption.series.name = "人口";
					this.mapOption.visualMap.max = 10000;
				} else if (this.dataType == 'gdp') {
					this.mapOption.title.text = this.barOption.title.text = "省份经济数据";
					this.mapOption.tooltip.formatter = this.barOption.tooltip.formatter =
						"<b>{a}</b> <br/>{b} ： <i>{c} 亿元</i>";
					this.mapOption.series.name = this.barOption.series.name = "GDP";
					this.mapOption.visualMap.max = 100000;
				} else if (this.dataType == 'salary') {
					this.mapOption.title.text = this.barOption.title.text = "省份工资数据";
					this.mapOption.tooltip.formatter = this.barOption.tooltip.formatter =
						"<b>{a}</b> <br/>{b} ： <i>{c} 元</i>";
					this.mapOption.series.name = this.barOption.series.name = "平均工资";
					this.mapOption.visualMap.min = 30000;
					this.mapOption.visualMap.max = 120000;
				} else if (this.dataType == 'house-price') {
					this.mapOption.title.text = this.barOption.title.text = "省份房价数据";
					this.mapOption.tooltip.formatter = this.barOption.tooltip.formatter =
						"<b>{a}</b> <br/>{b} ： <i>{c} 元/m²</i>";
					this.mapOption.series.name = this.barOption.series.name = "房价";
					this.mapOption.visualMap.min = 3000;
					this.mapOption.visualMap.max = 15000;
				}
				this.$axios.get(process.env.BASE_URL + 'data/chart/' + this.dataType + '.dat').then(resp => {
					this.dataList = eval(resp.data);
					this.setYearData();
				})
			},
			setTimer() {
				if (this.autoPlay) {
					this.timer = setInterval(() => {
						this.yearData = this.yearData == 2020 ? 2011 : (this.yearData + 1);
					}, 2000)
				} else {
					clearInterval(this.timer);
					this.timer = null;
				}
			}
		},
		mounted() {
			if (!this.$echarts.getMap('China')) {
				this.$axios.get(process.env.BASE_URL + 'map/china.json').then(map => {
					this.$echarts.registerMap('China', map.data)
					this.initChart();
				})
			} else {
				this.initChart();
			}
			this.$once("hook:beforeDestroy", () => {
				if (this.timer) {
					clearInterval(this.timer);
				}
			});
		},
		watch: {
			yearData() {
				this.setYearData();
			},
			dataType() {
				this.setDataInfo();
			},
			autoPlay() {
				this.setTimer();
			}
		}
	}
</script>

<style>
	.echart-box {
		margin: auto;
		width: 100%;
		height: 100%;
	}

	.show {
		animation: show 2s ease-in;
	}

	.label-text {
		text-align: center;
	}

	@keyframes show {
		from {
			opacity: 0;
		}
	}
</style>
